html {
  margin: 0;
  min-height: 100vh;
}

body {
  margin: 0;
  min-height: 100vh;
}

#root {
  min-height: 100vh;
}


.App {
  text-align: center;
}


@media only screen and (max-width: 400px) {
  div>.settings {
    min-height: 100%;
    max-height: 100%;
    width: 40%;
    background-color: black;
    color: white;
    position: absolute;
    left: 0;
    opacity: 0.8;
    overflow-y: scroll;
    overflow-x: hidden;
  }

  ul>li {
    font-size: 10px;
    border-bottom: solid 1px rgb(123, 255, 189);
    margin-top: auto;
    margin-bottom: 20px;
    margin-left: 0;
    margin-right: 20px;
    padding: 20px;
    cursor: pointer;
    font-stretch: ultra-condensed;
  }
  
  div>button {
    background-color: rgb(56, 56, 56);
    color: white;
    margin-top: 10px;
    margin-left: 16%;
    width: 100px;
    height: 30px;
    border: 0px;
    text-decoration: none;
    font-size: 15 px;
    cursor: pointer;
  }
}

.settings::-webkit-scrollbar {
  background: rgb(46, 46, 46);
  width: 10px;
  margin-top: 40px;
}
.settings::-webkit-scrollbar-thumb {
  background-color: rgb(0, 0, 0);
  -webkit-border-radius: 10px;
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5);
  box-shadow: inset 0 0 6px rgba(0,0,0,0.5);
}

.settings {
  min-height: 100%;
  max-height: 100%;
  width: 15%;
  background-color: black;
  color: white;
  position: absolute;
  left: 0;
  opacity: 0.8;
  overflow-y: scroll;
  overflow-x: hidden;
}

li {
  font-size: 18px;
  border-bottom: solid 1px rgb(123, 255, 189);
  margin-bottom: 20px;
  margin-left: 0;
  margin-right: 20px;
  padding: 20px;
  cursor: pointer;
}

ul {
  list-style-type: none;
}

ul li::before {
  content: "\2022";
  color: rgb(2, 223, 204);
  font-weight: bold;
  font-size: x-large;
  display: inline-block;
  width: 1em;
  margin-left: -1em;
}

button {
  background-color: rgb(56, 56, 56);
  color: white;
  margin-top: 10px;
  margin-left: 30%;
  width: 100px;
  height: 30px;
  border: 0px;
  text-decoration: none;
  font-size: 15 px;
  cursor: pointer;
}
